<template>
  <div id="imags">
    <el-container>
      <el-header height="30px">
        <div class="command">
          <div class="name">
            {{ currentData }}(
            {{ "已标注：" + unTag.total_tagging_pics }}
            {{ "图片总数：" + total }})
          </div>
          <div class="btns">
            <span class="add"
              ><add-pop @changeResult="getPage($event)"></add-pop
            ></span>
            <span class="upload">
              <up-load @changePageData="getPage($event)"></up-load
            ></span>
            <span class="dowmload"><down-load></down-load></span>
            <span class="showhidden">
              <el-button @click="isShowTag" type="primary" size="mini" round>{{
                btnName
              }}</el-button>
            </span>
            <span class="make">
              <el-button type="primary" size="mini" @click="handleMark" round
                >标注</el-button
              >
            </span>
            <!-- <ComDelete @changeList="handleDelete()"></ComDelete> -->
            <span class="delete">
              <el-button type="danger" size="mini" @click="handleDelete" round
                >删除</el-button
              >
            </span>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside width="180px">
          <el-col :span="24">
            <div class="right">
              <result-list></result-list>
            </div>
          </el-col>
        </el-aside>
        <el-container>
          <el-main>
            <!-- <div class="pic"> -->
            <el-row>
              <el-col
                :span="6"
                v-for="(item, col) in unTag.tagging_imgs"
                :key="item.imagename + col + time"
              >
                <div class="col-content">
                  <!-- :labelstatus="labelstatus"  :tagChinaName="currentTagKey" -->
                  <ShowHidden
                    :imgConfig="item"
                    :comIndex="col"
                    :chainName="chainName"
                    :key="item.imagename + col + time"
                  ></ShowHidden>
                  <!-- <el-image style="height: 180px; width: 260px" :src="item.url">
              </el-image> -->
                </div>
              </el-col>
            </el-row>
            <!-- </div> -->
          </el-main>
          <el-footer height="30px">
            <div class="footer">
              <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pageSize"
                :layout="layout"
                :total="total"
              >
              </el-pagination>
            </div>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import AddPop from "../DetailsComponent/AddPop.vue";
import ShowHidden from "../DetailsComponent/ShowHidden.vue";
import UpLoad from "../DetailsComponent/UpLoad.vue";
import DownLoad from "../DetailsComponent/DownLoad.vue";
import ResultList from "../DetailsComponent/ResultList";
// import ComDelete from "../DetailsComponent/ComDelete.vue";
// import tagImage from "../DetailsComponent/tagImage.vue";
export default {
  // name: "image",
  components: {
    AddPop,
    ShowHidden,
    UpLoad,
    DownLoad,
    ResultList,
    // ComDelete,
  },
  data() {
    return {
      // 当前数据集名称
      currentData: "",
      // 分页参数
      currentPage: 1, // 当前页码
      total: null, //总条数
      pageSize: 12, //当前每页条数
      pageSizes: [12, 18], //控制每页有多少条数
      layout: "total, sizes, prev, pager, next, jumper", //控制 分页组件的布局顺序
      // 图片展示列表信息
      unTag: {
        tagging_imgs: [],
        total_pages: 0,
        total_pic: 0,
        total_tagging_pics: 0,
        total_untagging_pics: 0,
      },
      time: null,
      // 显隐组件的传参信息
      labelstatus: "labeled", //已标注,未标注
      chainName: {}, //中文标注名
      chainNameKey: [], //chainName的键
      loading: "", //遮罩层
      currentTag: "全部标注", //当前显示的标注类型
      currentTagKey: "all",
      btn: "隐藏标注", //更换按钮的显示的文字
    };
  },
  computed: {
    // 选中一级类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // 二级导航栏
    menulist() {
      return this.$store.state.ItemDetails.menuList;
    },
    // 二级导航栏的选中值
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    //标注按钮的名称
    btnName() {
      return this.btn ? "隐藏标注" : "显示标注";
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    itemDate: {
      handler(newValue, oldValue) {
        if (this.currentType == "image") {
          console.log(newValue, oldValue);
          this.currentData = newValue;
          // console.log(this.currentData);
          this.unTag.tagging_imgs = [];
          this.currentPage = 1;
          // this.getTime();
          this.getPage();
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    // 刷新后数据集数据
    reLoad() {
      console.log(this.$store.state.ItemDetails.itemDate);
      this.currentData = this.$store.state.ItemDetails.itemDate;
      this.getPage();
    },
    getTime() {
      this.time = new Date().getTime();
      console.log("当前时间" + this.time + typeof this.time);
    },
    // 分页接口
    getPage() {
      const that = this;
      this.getTime();
      // this.currentType = this.$store.state.ItemDetails.currentType;
      // this.currentData = this.$store.state.ItemDetails.itemDate;
      if (this.$store.state.ItemDetails.currentType === "image") {
        this.$api
          .getPageData({
            dataSetName: this.currentData,
            page_number: this.currentPage,
            page_size: this.pageSize,
          })
          .then((res) => {
            this.loading = this.$loading({
              lock: true,
              text: "正在加载图片...",
              spinner: "el-icon-loading",
              background: "rgba(0, 0, 0, 0.7)",
            });
            console.log(res);
            if (res.code === 200 && res.dataType == "image") {
              console.log(res.images);
              //  that.unTag.tagging_imgs = res.images.tagging_imgs;
              let labels = res.images.tagging_imgs.map((item) => {
                let classes_count = Object.keys(item.classes_count).map(
                  (labels) => {
                    return { date: labels, name: item.classes_count[labels] };
                  }
                );
                return { ...item, classes_count };
              });
              console.log(labels);
              that.unTag = res.images;
              that.unTag.tagging_imgs = labels;
              that.total = parseInt(that.unTag.total_pics);
              that.unTag.total_tagging_pics = res.images.total_tagging_pics;
              that.unTag.total_untagging_pics = res.images.total_untagging_pics;
            }
            that.loading.close();
          });
      }
    },
    handleSizeChange(size) {
      console.log(`每页 ${size} 条`);
      this.pageSize = size;
      // this.getTime()
      this.getPage();
    },
    handleCurrentChange(page) {
      console.log(`当前页: ${page}`);
      this.currentPage = page;
      // this.getTime();
      this.getPage();
    },
    //隐藏标注
    isShowTag() {
      if (!this.btn) {
        $(".isShowLayer").css("display", "block");
      } else {
        $(".isShowLayer").css("display", "none");
      }
      this.btn = !this.btn;
    },
    // 跳转到标注页面
    handleMark() {
      this.$router.push({
        name: "TagImage",
      });
    },
    // 删除
    handleDelete() {
      console.log("开始删除");
      console.log(this.currentData);
      const that = this;

      this.$confirm("此操作将永久删除该数据集, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          that.$api
            .deleteData({ dataSetName: this.currentData })
            .then((res) => {
              console.log(res);
              this.$store.commit(
                "ItemDetails/delect_resultList_isDelect",
                false
              );
              this.$store.commit(
                "ItemDetails/delect_resultList_isDelect",
                true
              );
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "删除失败",
              });
            });
        })
        .catch((err) => {
          console.log(err);
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    // this.reLoad();
    // this.getPage();
  },
};
</script>
<style lang="scss" scope>
#imags {
  .el-container {
    .el-header {
      padding: 0;
      // margin-bottom: 0.5rem;
      .command {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 30px;
        color: #fff;
        padding: 0 2vh;
        background-color: #222337;
        .btns {
          display: flex;
          height: 30px;
          // background-color: #f5f4f8;
          justify-content: right;
          padding-right: 10px;
          span {
            margin-right: 5px;
          }
        }
        // .btns span {
        //   display: inline-block;
        //   padding: 0.3rem;
        // }
      }
    }
    .el-aside {
      // min-height: 860px;
      // max-height: 860px;
      overflow: hidden;
      height: calc(100vh - 132px);
    }

    .el-main {
      padding: 0;
      height: calc(100vh - 172px);
      overflow: hidden;
      // min-height: 745px;
      // max-height: 745px;
      // overflow: auto;
      .el-row {
        // height: calc(100vh - 172px);

        .el-col {
          padding-left: 1rem;
          &:nth-child(3n) {
            // margin-right: 0;
            padding-right: 0;
          }
        }
        .col-content {
          border-radius: 4px;
          margin: 8px 0;
          min-height: 36px;
          background: #fff;
          height: 12rem;
          // height: 240px;
        }
      }
    }
    .el-footer {
      padding: 0;
      margin-bottom: 10px;
      .footer {
        display: flex;
        justify-content: center;
        align-items: center;
        // height: 30px;
        background-color: #222337;
        // background-color: #adc4fa;
        // margin-top: 1rem;
        /deep/ .el-pagination__sizes {
          background: #4d4f62;
          color: #ffffff;
        }
        /deep/ .el-pagination span {
          background: #4d4f62;
          color: #ffffff;
        }
        /deep/ .el-pagination.is-background .el-pager li:not(.disabled) {
          background-color: #4d4f62;
          color: #fff;
        }
        /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
          background-color: green; // 进行修改选中项背景和字体
          color: #fff;
        }
        /deep/ .el-pagination.is-background .btn-prev {
          background-color: #4d4f62;
          color: #fff;
        }
        /deep/ .el-pagination.is-background .btn-next {
          background-color: #4d4f62;
          color: #fff;
        }
      }
    }
  }
}
</style>
