<template>
  <div>
    <el-button
      type="primary"
      size="mini"
      round
      @click="handleUpload"
      @closed="handleClose"
      >上传</el-button
    >
    <el-dialog
      title="上传数据"
      :close-on-click-modal="false"
      :visible.sync="dialogFormVisible"
    >
      <el-radio-group v-model="mode" @change="changeMode">
        <el-radio :label="1">上传图片</el-radio>
        <el-radio :label="2">上传标注文件xml</el-radio>
      </el-radio-group>
      <!--    accept=".jpg" -->
      <el-upload
        class="upload-demo"
        ref="upload"
        action="#"
        :http-request="httpReq"
        :before-upload="beforeUpload"
        :on-error="uploadError"
        :auto-upload="false"
        :limit="3000"
        multiple
      >
        <div class="el-upload__bts">
          <el-button slot="trigger" size="mini" type="primary"
            >选取文件</el-button
          >
          <el-button
            style="margin-left: 10px"
            size="mini"
            type="success"
            @click="submitUpload"
            >上传到服务器</el-button
          >
        </div>

        <div slot="tip" class="el-upload__tip" v-show="mode === 1">
          <div>1、只能上传jpg文件，且不超过1200M</div>
          <div>2、一次只能上传3000张</div>
          <div>3、上传过程中不要关闭页面并报持网络畅通</div>
        </div>
        <div slot="tip" class="el-upload__tip" v-show="mode === 2">
          <div>1、只能上传xml文件</div>
        </div>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="hideUpload">取 消</el-button>
        <el-button type="primary" size="mini" @click="determineUpload"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 当前数据集名称
      currentDateName: "",
      // 选中上传方式
      mode: 1,
      // 控制上传弹窗显隐
      dialogFormVisible: false,
      // 图片上传时的参数
      postImgParam: {
        datasetname: "",
        action: "",
        version: "",
        index: "",
      },
    };
  },
  computed: {
    // 二级导航栏的选中值
    currentName() {
      return this.$store.state.ItemDetails.itemDate;
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    currentName: {
      handler(newValue, oldValue) {
        console.log(newValue, oldValue);
        this.currentDateName = newValue;
      },
    },
  },
  methods: {
    // 上传方式
    changeMode() {
      console.log("上传的方式变化了" + this.mode);
    },
    // 点击上传按钮
    handleUpload() {
      console.log("点击上传");
      this.dialogFormVisible = true;
    },
    beforeUpload(file) {
      var testfile = file.name.substring(file.name.lastIndexOf(".") + 1);
      // const type = "";
      if (this.mode == 1) {
        console.log("jpg");
        testfile = "jpg";
      } else {
        console.log("xml");
        testfile = "xml";
      }
      // const type = (testfile === 'jpg') || (testfile === 'xml'); // 限制文件类型
      const size = file.size / 1024 / 1024 < 10; // 限制文件大小
      if (!testfile) {
        this.$message({
          message: "文件类型只能是 jpg 或者 xml格式!",
          type: "warning",
        });
      }
      if (!size) {
        this.$message({
          message: "文件大小不能超过10MB!",
          type: "warning",
        });
      }
      return testfile && size;
    },
    // 手动上传图片时,执行的上传方法
    httpReq(param) {
      // if (this.mode == 1) {
      console.log("httpReq");
      console.log(param);

      this.currentDateName = this.currentName;
      console.log(this.currentDateName);
      // const that = this;
      let fileObj = param.file; // 相当于input里取得的files
      let fd = new FormData(); // FormData 对象
      if (param.file.type == "image/jpeg") {
        console.log("pic");
        fd.append("file", fileObj); // 文件对象
        fd.append("dataSetName", this.currentDateName); //数据集名称

        this.$api.uploadImgs(fd).then((res) => {
          if (res.code == 200) {
            console.log(res);
            this.$refs.upload.uploadFiles =
              this.$refs.upload.uploadFiles.filter((item) => {
                return param.file.name != item.name;
              });
          } else if (res.code == 404) {
            this.$message({
              message: "该图片已存在",
              type: "warning",
            });
          }
        });
      } else {
        console.log("xml");
        let fd = new FormData(); // FormData 对象
        fd.append("labelFile", fileObj); // 文件对象
        fd.append("dataSetName", this.currentDateName); //数据集名称
        this.$api.uploadXmls(fd).then((res) => {
          console.log(res);
          this.$refs.upload.uploadFiles = this.$refs.upload.uploadFiles.filter(
            (item) => {
              // return param.labelFile.name != item.name;
              return param.file.name != item.name;
            }
          );
        });
      }
    },
    httpReqXml(param) {
      console.log("httpReqXml");
      console.log(param);
      console.log(this.currentDateName);
      this.currentDateName = this.currentName;
      let fileObj = param.file; // 相当于input里取得的files
      let fd = new FormData(); // FormData 对象
      fd.append("labelFile", fileObj); // 文件对象
      fd.append("dataSetName", this.currentDateName); //数据集名称
      this.$api.uploadXmls(fd).then((res) => {
        console.log(res);
        this.$refs.upload.uploadFiles = this.$refs.upload.uploadFiles.filter(
          (item) => {
            return param.file.name != item.name;
          }
        );
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    // 手动上传到服务器
    submitUpload(val) {
      console.log("执行了");
      console.log(val);
      this.$refs.upload.submit();
    },
    // 上传失败
    uploadError(file, fileList) {
      console.log(fileList);
    },
    // 对话框关闭回调事件的触发
    handleClose() {},
    // 点击弹窗取消按钮
    hideUpload() {
      const that = this;
      that.$refs.upload.uploadFiles = [];
      that.dialogFormVisible = false;
    },
    // 点弹窗确定按钮
    determineUpload() {
      const that = this;
      console.log(that.$refs.upload.uploadFiles.length);
      if (that.$refs.upload.uploadFiles.length > 0) {
        that.$message({
          type: "error",
          message: "部分图片上传失败",
        });
      }
      that.$refs.upload.uploadFiles = [];
      that.dialogFormVisible = false;
      this.$emit("changePageData", this.currentName);
      // that.getData();
    },
  },
};
</script>
<style lang="scss" scoped>
.el-upload__bts {
  margin: 10px 0;
}
.el-upload__tip {
  color: red;
}
</style>
