<template>
  <!-- 展示标注图片的组件 -->
  <div id="tagImged">
    <div class="imgs">
      <div :id="'threeTag' + comIndex" class="threeTag" ref="threeTag"></div>
      <img id="biaoZu" ref="biaoZu" :src="comPath" alt="暂无图片" />
      <canvas
        :id="'myCanvas' + comIndex"
        class="myCanvas"
        ref="myCanvas"
      ></canvas>
    </div>
    <!-- <el-table
      :data="[...imgConfig.classes_count]"
      class="labels"
      max-height="220px"
      max-width="160px"
      :row-style="{ height: '34px' }"
      :cell-style="{ padding: '0' }"
    >
      <el-table-column prop="date" label="类别" width="90"></el-table-column>
      <el-table-column prop="name" label="数量" width="50"></el-table-column>
    </el-table> -->
  </div>
</template>

<script>
export default {
  name: "ShowHidden",
  props: {
    imgConfig: {
      type: Object,
      default() {
        return {};
      },
    },
    // 标签表格数据
    classes_count: {
      type: Array,
      default() {
        return [];
      },
    },
    comIndex: {
      type: Number,
      default: 1,
    },
    chainName: {},
    // tagChinaName: "",
    // labelstatus: "",
  },
  data() {
    return {
      imgPath: "",
      ratio: 1,
      imgWH: {
        width: "",
        height: "",
      },
      layer: 0,
      haveCanvas: true,
      imgHeight: 200,
      imgWidth: 335,
    };
  },
  computed: {
    imgData() {
      return this.imgConfig;
    },
    comPath() {
      return this.imgPath;
    },
    currentData() {
      return this.$store.state.ItemDetails.itemDate;
    },
  },
  watch: {
    imgConfig: {
      handler(newVal) {
        this.initImage(newVal);
        console.log("初始化");
      },

      deep: true,
    },
  },
  methods: {
    //图片加载前,修改宽高
    resizeImgV2(imgItem) {
      $("#threeTag" + this.comIndex).empty();
      let imgHeight = parseInt(imgItem["imagesize"].split(",")[1]);
      let imgWidth = parseInt(imgItem["imagesize"].split(",")[0]);
      const that = this;
      that.imgWH.width = imgWidth;
      that.imgWH.height = imgHeight;
      let width = "";
      let height = "";
      let ratio = that.imgHeight / imgHeight;
      that.ratio = ratio;
      console.log("按高比例缩放" + ratio);
      width = parseInt(ratio * imgWidth);
      height = parseInt(ratio * imgHeight);
      // console.log(width, that.imgWidth);
      if (width > that.imgWidth) {
        ratio = that.imgWidth / imgWidth;
        that.ratio = ratio;
        console.log("按宽比例缩放" + ratio);
        width = parseInt(ratio * imgWidth);
      }
      height = parseInt(that.ratio * imgHeight);
      //图片
      this.$refs["biaoZu"].style.height = `${height}px`;
      this.$refs["biaoZu"].style.width = `${width}px`;
      // 画布
      this.$refs["myCanvas"].setAttribute("width", `${width}px`);
      this.$refs["myCanvas"].setAttribute("height", `${height}px`);
      this.$refs["threeTag"].style.height = `${height}px`;
      this.$refs["threeTag"].style.width = `${width}px`;
      this.createTag();
      // 将父组件的传过来路径赋值
      this.imgPath = imgItem.imagename;
    },
    //加载图片
    addImgV2(imgItem) {
      console.log(imgItem);
      const that = this;
      console.log(imgItem["imagesize"]);
      // 图片实际宽度
      let imgHeight = parseInt(imgItem["imagesize"].split(",")[1]);
      let imgWidth = parseInt(imgItem["imagesize"].split(",")[0]);
      // this.haveCanvas = false
      that.imgWH.width = imgWidth;
      that.imgWH.height = imgHeight;
      let width = "";
      let height = "";
      let ratio = "";
      ratio = that.imgHeight / imgHeight;
      that.ratio = ratio;
      console.log("按高比例缩放" + ratio);
      width = parseInt(ratio * imgWidth);
      console.log(width, that.imgWidth);
      // 340px 是盒子的宽度的70%的像素
      if (width > that.imgWidth) {
        ratio = that.imgWidth / imgWidth;
        that.ratio = ratio;
        console.log("按宽比例缩放" + ratio);
        width = parseInt(ratio * imgWidth);
      }
      height = parseInt(that.ratio * imgHeight);
      this.$refs["biaoZu"].style.height = `${height}px`;
      this.$refs["biaoZu"].style.width = `${width}px`;
      this.imgPath = imgItem.imagename;
    },
    //生成标注
    createTag() {
      const that = this;
      let threeTag = "threeTag" + that.comIndex;
      let ratio = that.ratio;
      let context = that.$refs["myCanvas"].getContext("2d");
      context.strokeStyle = "#FF0000";
      this.imgConfig.labelboxes.forEach((item) => {
        //计算缩放后的值
        let x = parseInt(item.xmin * ratio);
        let y = parseInt(item.ymin * ratio);
        let width = parseInt((item.xmax - item.xmin) * ratio);
        let height = parseInt((item.ymax - item.ymin) * ratio);
        // console.log({ x, y, width, height })
        //不存在对应的中文名称,则显示英文
        let tagName = that.chainName[item.label]
          ? that.chainName[item.label]
          : item.label;
        that.layer += 1;
        // 添加标注标签
        let div = $(
          `<div id="layer${that.layer}" class="isShowLayer">${tagName}</div>`
        );
        div.css({
          position: "absolute",
          background: "rgb(107,124,152)",
          left: `${x}px`,
          top: `${y}px`,
          color: "yellow",
          "z-index": 1000,
        });
        // 添加方框
        context.strokeRect(x, y, width, height);
        $("#" + threeTag).append(div);
      });
    },
    // 判断是已标注还是未标注
    initImage(imgConfig) {
      $("#threeTag" + this.comIndex).empty();
      this.$refs["myCanvas"].setAttribute("width", `${200}px`);
      this.$refs["myCanvas"].setAttribute("height", `${200}px`);
      if (this.imgConfig.labelboxes.length != 0) {
        console.log("***已标注*****");
        this.haveCanvas = true;
        this.resizeImgV2(imgConfig);
      } else {
        this.haveCanvas = false;
        this.addImgV2(imgConfig);
      }
    },
  },
  mounted() {
    this.initImage(this.imgConfig);
  },
  // activated() {
  //   this.initImage(this.imgConfig);
  // },
};
</script>

<style lang="scss" scoped>
#tagImged {
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
  border: 1px solid rgb(95, 209, 253);
  .imgs {
    // position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    // width: 70%;
    // position: absolute;

    .myCanvas {
      position: absolute;
      z-index: 100;
      display: block;
      // margin: auto;
      // width: 70%;
      // top: 0;
      // bottom: 0;
      // left: 0;
      // right: 0;
      // margin: 0 auto;
    }
    #biaoZu {
      position: absolute;
      display: block;
      // margin: auto;
      // width: 70%;
      // top: 0;
      // bottom: 0;
      // left: 0;
      // right: 0;
      // margin: 0 auto;
      z-index: -1;
    }
    .threeTag {
      position: relative;
      // position: absolute;
      // width: 70%;
      // top: 0;
      // bottom: 0;
      // left: 0;
      // right: 0;
      // margin: 0 auto;
      // overflow: hidden;
    }
  }

  // .labels {
  //   position: absolute;
  //   width: 30%;
  //   top: 0;
  //   left: 70%;
  //   bottom: 0;
  //   // overflow-y: auto;
  //   overflow-x: hidden;
  // }
  .hide {
    display: none;
  }
  .show {
    display: block;
  }
}
</style>
